import React, { Component } from "react";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { connect } from "react-redux";
import { createNumberMask } from "redux-form-input-masks";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { SkeletonLoading } from "../../GlobalComponent/function";
import { setFocus } from "../../helper";
import { getListBarang } from "../../actions/kodeBarangAction";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class FormTolakanCustomer extends Component {
  constructor(props) {
    super(props);
    this.onclick1 = this.onclick1.bind(this);
    this.state = {
      kodeBarang: [],
      DataSupplier: [],
      non_aksesoris: "col-lg-12 row",
      aksesoris: "col-lg-12 row d-none",
      kode_barang: "",
      keterangan: "INI KETERANGAN",
      kadar: "",
      P: "",
      K: "",
      bungkus: "",
      berat: "",
      harga: "",
      murni: "",
      ongkos: "",
      status: "",
      kadarCetak: "",
      kadarBeli: "",
      namaJenis: "",
      kodeJenis: "",
      showMenu: true,
      isBarangToko: true,
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("gross", this.props.gross);
  }

  pilihBarang(data) {
    let kodeCustomer = this.props.kodeSupplier;
    let param = data;
    let hasil = this.props.KodeBarangList.filter(
      (data) => data.kode_jenis === param
    );
    AxiosMasterGet(
      `/kelompok-harga/get/by-jenis-and-customer/${kodeCustomer}&${data}&PUSAT`
    ).then((res) => {
      if (res.data?.kode_kelompok === "AKSESORIS") {
        this.setState({
          non_aksesoris: "col-lg-12 row d-none",
          aksesoris: "col-lg-12 row",
        });
      } else {
        this.setState({
          non_aksesoris: "col-lg-12 row",
          aksesoris: "col-lg-12 row d-none",
        });
      }
      this.setState({
        kodeJenis: hasil[0].kode_jenis,
        namaJenis: hasil[0].nama_jenis,
        kadarCetak: res.data?.kode_tukar_normal,
        kadarJual: res.data?.kode_tukar_normal,
        kodeKategori: res.data?.kode_kategori,
        namaKategori: res.data?.nama_kategori,
        harga_jual: res.data?.harga_aksesoris,
      });
      this.setKadar();
      setFocus("P");
    });
  }

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarJual);
    this.props.change("kode_kategori", this.state.kodeKategori);
    this.props.change("harga_jual", this.state.harga_jual);
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
        <div
            className={"col-lg-12"}
          >
            <div className="col-md-12 col-lg-12 d-none">
              <Field
                name="noFaktur"
                label={this.props.noFaktur}
                type="text"
                readOnly={true}
                component={renderField}
                defaultValue={this.props.noFaktur}
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div
              className={
                this.state.isBarangToko
                  ? "col-md-4 form-group"
                  : "col-md-4 form-group d-none"
              }
            >
              {String(this.props.KodeBarangList) !== "" ? (
                <Field
                  label="Kode Model"
                  name="kodeJenis"
                  component={renderFieldSelect}
                  options={this.props.KodeBarangList.map((list) => {
                    let data = {
                      value: list.kode_jenis,
                      name: list.nama_jenis,
                    };
                    return data;
                  })}
                  onChange={(data) => this.pilihBarang(data)}
                  onBlur={this.setKadar()}
                  placeholder="Silahkan Pilih"
                ></Field>
              ) : (
                <SkeletonLoading label="Kode Model" />
              )}
            </div>
            <div
              className="col-md-8 form-group"
              style={
                this.props.from === "terima" ? null : { visibility: "hidden" }
              }
            >
              <Field
                name="keterangan"
                label="Keterangan"
                type="text"
                component={renderField}
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className={this.state.non_aksesoris}>
              <div className="col-md-3 form-group">
                <Field
                  name="kadar"
                  label="Cap Kode"
                  type="number"
                  component={renderField}
                  readOnly={true}
                />
              </div>
              <div className="col-md-3 form-group">
                <Field
                  name="p"
                  label="P"
                  type="number"
                  component={renderField}
                />
              </div>
              <div className="col-md-3 form-group">
                <Field
                  name="k"
                  label="K"
                  type="number"
                  component={renderField}
                />
              </div>
              <div className="col-md-3 form-group">
                <Field
                  name="pkg"
                  label="Bungkus (Gr)"
                  type="number"
                  component={renderField}
                  onBlur={() => this.onclick1()}
                />
              </div>
              <div className="col-md-3 form-group">
                <Field
                  name="bruto"
                  label="Berat (Gr)"
                  type="number"
                  component={renderField}
                  onBlur={() => this.onclick1()}
                />
              </div>
              <div
                className={
                  this.state.isBarangToko
                    ? "col-md-4 form-group"
                    : "col-md-4 form-group d-none"
                }
              >
                <Field
                  name="kadarTransaksi"
                  label="Kode Awal (%)"
                  type="number"
                  component={renderField}
                  onBlur={() => this.onclick1()}
                />
              </div>
              <div
                className={
                  this.state.isBarangToko
                    ? "col-md-4 form-group"
                    : "col-md-4 form-group d-none"
                }
              >
                <Field
                  name="gross"
                  label="Gross (Gr)"
                  type="number"
                  component={renderField}
                  readOnly={true}
                />
              </div>
              <div
                className={
                  this.state.isBarangToko
                    ? "col-md-4 form-group"
                    : "col-md-4 form-group d-none"
                }
              >
                <Field
                  name="netto"
                  label="Murni (Gr)"
                  type="number"
                  component={renderField}
                  readOnly={true}
                />
              </div>
              <div className="col-md-3 form-group d-none">
                <Field
                  name="kode_kategori"
                  label="Kode Kategori"
                  type="number"
                  component={renderField}
                  readOnly={true}
                />
              </div>
              <div className="col-md-3 form-group">
                <label htmlFor="">Ongkos (Rp)</label>
                <Field
                  name="rp"
                  component="input"
                  type="tel"
                  className="form-control"
                  {...currencyMask}
                />
              </div>
            </div>
            <div className={this.state.aksesoris}>
              <div className="col-md-4 form-group">
                <Field
                  name="qty"
                  label="QTY"
                  type="number"
                  component={renderField}
                />
              </div>
              <div className="col-md-4 form-group">
                <label htmlFor="">Harga Beli (Rp)</label>
                <Field
                  name="harga_jual"
                  component="input"
                  type="tel"
                  className="form-control"
                  {...currencyMask}
                />
              </div>
            </div>
            <div className="col-md-12 form-group mt-3">
              <div className="text-right">
                <button
                  className="btn btn-primary"
                  type="submit"
                  disabled={this.props.submitting}
                >
                  Simpan
                </button>
              </div>
            </div>
          </div>
          
        </div>
      </form>
    );
  }
}

FormTolakanCustomer = reduxForm({
  form: "TerimaSupplier",
  enableReinitialize: true,
})(FormTolakanCustomer);
const selector = formValueSelector("TerimaSupplier"); // <-- same as form name

export default connect((state) => {
  const { bruto, kadarTransaksi, pkg } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "pkg"
  );
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    gross: parseFloat(bruto) + parseFloat(pkg),
    KodeBarangList: state.provinsi.kodeBarang,
    initialValues: {
      discGlobalPr: 0,
      p: 0,
      k: 0,
      pkg: 0,
      bruto: 0,
      rp: 0,
    },
  };
})(FormTolakanCustomer);
