import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { getUUID } from "../actions/fakturAction";
import Swal from "sweetalert2";
import { getListTolakReturnCustomer } from "../actions/returnSupplier";
import { Field, formValueSelector, reduxForm, reset } from "redux-form";

import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";
import { deleteIndex } from "../deleteLocalStorage";
import ReactTooltip from "react-tooltip";
import { finishSend, progressSend } from "../actions/LogicAction";
import { AxiosMasterPost } from "../Axios";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";
import { renderField, renderFieldSelect } from "../renderField";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import {
  formatGram,
  formatRp,
  getUserID,
  multipleDeleteLocal,
} from "../helper";
import { getcustomerList } from "../actions/customerAction";
import FormTolakanCustomer from "./Component/FormTolakanCustomer";
import { getToday } from "../getDate";

const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);

class TolakReturnCustomer extends Component {
  constructor(props) {
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    super(props);
    this.state = {
      supplierList: [],
      DataSupplier: [],
      startDate: formatedDate,
      kodeSupplier: "",
      namaSupplier: "",
      tanggal: formatedDate,
      noBon: "",
      status: "",
      tampungBarang: [],
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
        },
        {
          dataField: "kadar_cetak",
          text: "Cap\nKode",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "p",
          text: "P",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "k",
          text: "K",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "pkg",
          text: "Bungkus\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "kadar_beli",
          text: "Kode\nAwal",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "netto",
          text: "Murni\n(Gr)",
          align: "right",
          headerAlign: "right",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos\n(RP)",
          align: "right",
          headerAlign: "right",
          formatter: (cell) => (
            <span>Rp. {parseFloat(cell).toLocaleString("ID-id")}</span>
          ),
        },
        {
          dataField: "harga_jual",
          text: "Harga Jual)",
          align: "right",
          headerAlign: "right",
          formatter: (cell) => (
            <span>Rp. {parseFloat(cell).toLocaleString("ID-id")}</span>
          ),
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndex(
                      "TolakReturnCustomer",
                      rowIndex,
                      this.props.dispatch,
                      getListTolakReturnCustomer()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    localStorage.removeItem("TolakReturnCustomer")
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getListTolakReturnCustomer());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  handleSubmit(data) {
    var supp = data.kodeJenis
      ? data.kodeJenis
      : "DEFAULT|DEFAULT|DEFAULT|DEFAULT";
    var split = supp.split("|");
    var simpan = [];
    if (data.kodeJenis !== undefined) {
      simpan = {
        kode_kategori: data.kode_kategori,
        kode_jenis: split[0],
        kadar_cetak: (parseFloat(data.kadar) || 0).toString(),
        kadar_beli: parseFloat(data.kadarTransaksi) || 0,
        no_barcode: "-",
        qty: data.qty || 1,
        p: parseFloat(data.p) || 0,
        k: parseFloat(data.k) || 0,
        pkg: parseFloat(data.pkg) || 0,
        gross: parseFloat(data.gross) || 0,
        bruto: parseFloat(data.bruto) || 0,
        netto: parseFloat(data.netto) || 0,
        ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp),
        harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
      };
    } else {
      simpan = {
        kode_kategori: "-",
        kode_jenis: "-",
        kadar_cetak: "100",
        kadar_beli: 100,
        no_barcode: "-",
        qty: 1,
        p: parseFloat(data.p) || 0,
        k: parseFloat(data.k) || 0,
        pkg: parseFloat(data.pkg) || 0,
        gross: parseFloat(data.bruto) + parseFloat(data.pkg) || 0,
        bruto: parseFloat(data.bruto) || 0,
        netto: parseFloat(data.bruto) || 0,
        ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp) || 0,
        harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual || 0,
      };
    }

    var array = JSON.parse(localStorage.getItem("TolakReturnCustomer")) || [];
    array.push(simpan);
    localStorage.setItem("TolakReturnCustomer", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.dispatch(getListTolakReturnCustomer());
    this.props.dispatch(reset("TerimaSupplier"));
  }

  returnSupplierSubmit() {
    if (this.props.no_bon_selected === "") {
      warning("Mohon isi nomor bon");
      return false;
    } else if (this.props.tanggal_selected === "") {
      warning("Mohon isi Tanggal");
      return false;
    } else if (localStorage.getItem("TolakReturnCustomer") === null) {
      warning("Barang masih kosong, silahkan tambahkan barang");
      return false;
    } else if (this.props.supplier_selected === "") {
      warning("Mohon pilih Supplier");
      return false;
    }
    let data = JSON.parse(localStorage.getItem("TolakReturnCustomer"));
    let qty = data
      .map((list) => parseFloat(list.qty))
      .reduce((a, b) => a + b, 0);
    let p = data.map((list) => parseFloat(list.p)).reduce((a, b) => a + b, 0);
    let k = data.map((list) => parseFloat(list.k)).reduce((a, b) => a + b, 0);
    let pkg = data
      .map((list) => parseFloat(list.pkg))
      .reduce((a, b) => a + b, 0);
    let bruto = data
      .map((list) => parseFloat(list.bruto))
      .reduce((a, b) => a + b, 0);
    let netto = data
      .map((list) => parseFloat(list.netto))
      .reduce((a, b) => a + b, 0);
    let ongkos_rp = data
      .map((list) => parseFloat(list.ongkos_rp))
      .reduce((a, b) => a + b, 0);
    let isi = {
      no_tolak: this.props.noFaktur,
      tanggal: this.props.tanggal_selected,
      no_bon: this.props.no_bon_selected,
      kode_customer: this.props.supplier_selected,
      jenis_trx: "RETUR",
      total_qty: qty,
      total_p: p,
      total_k: k,
      total_pkg: pkg,
      total_bruto: bruto,
      total_netto: netto,
      total_ongkos: ongkos_rp,
      detail_barang: JSON.parse(localStorage.getItem("TolakReturnCustomer")),
    };
    console.log(JSON.stringify(isi));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("TolakReturnCustomer"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kode_jenis,
        { content: ticket.kadar_cetak, styles: { halign: "right" } },
        { content: ticket.qty, styles: { halign: "right" } },
        { content: formatGram(ticket.p), styles: { halign: "right" } },
        { content: formatGram(ticket.k), styles: { halign: "right" } },
        { content: formatGram(ticket.pkg), styles: { halign: "right" } },
        { content: formatGram(ticket.bruto), styles: { halign: "right" } },
        { content: ticket.kadar_beli, styles: { halign: "right" } },
        { content: formatGram(ticket.netto, 3), styles: { halign: "right" } },
        { content: formatRp(ticket.ongkos_rp), styles: { halign: "right" } },
        { content: formatRp(ticket.harga_jual), styles: { halign: "right" } },
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      {
        content: table
          .map((list) => parseFloat(list.qty))
          .reduce((a, b) => a + b, 0),
        styles: { halign: "right" },
      },
      "",
      "",
      {
        content: formatGram(
          table.map((list) => parseFloat(list.pkg)).reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
      {
        content: formatGram(
          table.map((list) => parseFloat(list.bruto)).reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
      "",
      {
        content: formatGram(
          table
            .map((list) => parseFloat(list.netto))
            .reduce((a, b) => a + b, 0),
          3
        ),
        styles: { halign: "right" },
      },
      {
        content: formatRp(
          table.map((list) => list.ongkos_rp).reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
      {
        content: formatRp(
          table
            .map((list) => parseFloat(list.harga_jual))
            .reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
    ];
    // tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      [
        "No",
        "Kode Model",
        { content: "Cap Kode", styles: { halign: "right" } },
        { content: "Qty", styles: { halign: "right" } },
        { content: "P", styles: { halign: "right" } },
        { content: "K", styles: { halign: "right" } },
        { content: "Bungkus\n(Gr)", styles: { halign: "right" } },
        { content: "Berat\n(Gr)", styles: { halign: "right" } },
        { content: "Harga\n(Gr)", styles: { halign: "right" } },
        { content: "Murni\n(Gr)", styles: { halign: "right" } },
        { content: "Ongkos\n(Rp)", styles: { halign: "right" } },
        { content: "Harga\nJual(Rp)", styles: { halign: "right" } },
      ],
    ];
    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("trx-tolakan-customer/simpan", isi)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() =>
            PrintNota(
              "No Bon",
              this.props.no_bon_selected,
              "Tanggal",
              this.props.tanggal_selected,
              "Customer",
              this.props.supplier_selected,
              "",
              "",
              getUserID(),
              getToday(),
              "",
              columTable,
              "Tolakan Return Customer",
              tableRows,
              footer
            )
          )
          .then(() => multipleDeleteLocal(["TolakReturnCustomer", "noFaktur"]))
          .then(() => this.props.dispatch(reset("TolakReturnCustomer")))
          .then(() => this.props.dispatch(finishSend()))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getListTolakReturnCustomer()))
          .then(() => window.location.reload())
          .then(() =>
            this.setState({
              noBon: "",
              kodeSupplier: "",
            })
          )
          .then(() => document.getElementById("myForm").reset())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: err?.response.data,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Tolakan Return Customer" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    <Suspense
                      fallback={<Skeleton width={"100%"} height={400} />}
                    >
                      <FormTolakanCustomer
                        from="tolakan"
                        kodeSupplier={this.props.supplier_selected}
                        onSubmit={(data) => this.handleSubmit(data)}
                        noFaktur={this.props.noFaktur}
                      />
                    </Suspense>
                  }
                  title="Tambah Data Barang"
                />
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-12">
                      <form id="myForm">
                        <div className="row">
                          <div className="col-lg-4">
                            <Field
                              name="supplier"
                              component={renderFieldSelect}
                              options={this.props.customer.map((list) => {
                                let data = {
                                  value: list.kode_customer,
                                  name: list.nama_customer,
                                };
                                return data;
                              })}
                              type="text"
                              label="Customer"
                              placeholder="Masukan Customer"
                            />
                          </div>
                          <div className="col-lg-4">
                            <Field
                              name="tanggal"
                              component={renderField}
                              type="date"
                              label="Tanggal"
                              placeholder="Masukan Tanggal"
                            />
                          </div>
                          <div className="col-lg-4">
                            <Field
                              name="no_bon"
                              normalize={(data) => data?.toUpperCase()}
                              component={renderField}
                              type="text"
                              label="Nomor Bon"
                              placeholder="Masukan Nomor Bon"
                            />
                          </div>
                        </div>
                      </form>
                    </div>
                    <div className="col-md-12 col-lg-12 mt-3">
                      <div className="row">
                        <div className="col-lg-6"></div>
                        <div className="col-lg-6">
                          <div className="text-right">
                            <button
                            disabled={this.props.supplier_selected === undefined}
                              className="btn btn-primary"
                              onClick={() => this.props.dispatch(showModal())}
                            >
                              Tambah Data
                              <i class="fa fa-plus ml-3" aria-hidden="true"></i>
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-12 mt-3">
                      <GlobalTabel
                        keyField="id"
                        data={this.props.listTolakanReturnCustomer}
                        columns={this.state.columns}
                      />
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          disabled={this.props.onSend}
                          onClick={() => this.returnSupplierSubmit()}
                        >
                          {this.props.onSend ? (
                            <span
                              className="spinner-border spinner-border-sm mr-3"
                              aria-hidden="true"
                            ></span>
                          ) : null}
                          Selesai
                <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
TolakReturnCustomer = reduxForm({
  form: "TolakReturnCustomer",
  enableReinitialize: true,
})(TolakReturnCustomer);
const selector = formValueSelector("TolakReturnCustomer");
export default connect((state) => {
  const { tanggal, supplier, no_bon } = selector(
    state,
    "tanggal",
    "supplier",
    "no_bon"
  );
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    listTolakanReturnCustomer: state.provinsi.listTolakanReturnCustomer,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
    supplier: state.provinsi.supplier,
    customer: state.provinsi.customer,
    tanggal_selected: tanggal,
    supplier_selected: supplier,
    no_bon_selected: no_bon,
    initialValues: {
      tanggal: getToday(),
    },
  };
})(TolakReturnCustomer);
